.header {
  position: relative;
}
.header img {
  width: 100%;
  height: auto;
}
.logo {
  cursor: pointer;
}
.nav-container {
  position: absolute;
  top: 0.7rem;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  /* background-color: lightpink; */
  margin: 0 2rem;
  padding: 1rem 1.5rem;
}
.nav-container .logo img {
  width: 100%;
}

.nav-container .navbar {
  display: flex;
  margin-left: 16rem;
  align-items: center;
}

.nav-container .navbar .list {
  display: none;
  background-color: rgb(0, 162, 255);
  width: 200px;
  font-size: 0.7rem;

}

.navbar li a{
  text-decoration: none;
  color: black;
  cursor: pointer;
}

.navbar li {
  list-style-type: none;
  padding: 0 1rem;
}

.navbar button {
  padding: .7rem 1.5rem;
  /* box-shadow: ; */
  border: none;
  border-radius: 6px;
  font-weight: bold;
  color: white;
  cursor: pointer;
  box-shadow: 0 0 1px 0 rgba(0,0,0,0.4);
}

.listing {
  background-color: rgb(0, 162, 255);
  margin-left: 2rem;
}

.login {
  background-color: orange;
  margin-left: 2rem;
}

/* list your property  */
.property-container {
  background-color: rgb(73, 178, 248);
  padding: 1rem 5rem;
}

.property-box {
  /* background-color: yellow; */
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.property-box .detail-box {
  width: 50%;
}
.property-box p {
  font-size: 15px;
}


.property-box h1 {
  color: whitesmoke;
  font-size: 3rem;
  font-weight: 500;
}

.property-btn {
  padding: .9rem 2rem;
  background-color: white;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  color: rgb(0, 153, 255);
  margin-top: 4rem;
}

.property-image img {
  width: 100%;
  height: auto;
}

/* testimonial section  */
.testimonial {
  background-color: whitesmoke;
}
.testimonial h1 {
  letter-spacing: 2px;
  font-weight: 500;
  font-size: 2.5rem;
}

/* choose section  */
.choose-heading {
  background-color: whitesmoke;
  padding-top: 1.5rem;
}
.choose-heading h1 {
  color: rgb(122, 206, 255);
  font-weight: 500;
  font-size: 2.5rem;
}

.choose-heading .choose-title {
  color: white;
}

@media screen and (max-width: 1024px){
  .nav-container .navbar {
      display: none;
    }
    .nav-container .navbar .list {
      display: block;
    }
}
